.main_title{width: 100%; height: 70px; color: #eee; font-size: 2.2rem; font-weight: bold; line-height: 70px; margin: 0; padding-left: 50px; text-shadow: 0px 0px 15px #fff;}
@charset "utf-8";

/*.home_wrap*/
.home_wrap{width: 100%; height: 100%; position: relative;}
.home_wrap p{position: absolute; top: 50%; left: 50%; -webkit-transform: translate(-50%, -50%); transform: translate(-50%, -50%); font-size: 10rem; color: #eee; font-weight: bold}

@charset "utf-8";

/*.loginBox*/
.login_wrap{width: 100%; height: calc(100% - 70px); position: relative;}
.login_wrap form{width: 410px; padding: 50px 30px; position: absolute; top: 40%; left: 50%; -webkit-transform: translate(-50%, -50%); transform: translate(-50%, -50%); border: 1px solid #fff;}
.login_wrap form:after{content: ""; display: block; clear: both;}
.login_wrap form > div{float: left;}
.login_wrap form > div > input{display: block; width: 260px; height: 40px; padding-left: 25px; font-weight: 300 !important; border: 1px solid #fff; background: transparent; color: #fff;}
.login_wrap form > div > input::-webkit-input-placeholder{color: #a0a0a0; font-size: 1.5rem; font-family: NanumBarunGothic; color: #eee !important;}
.login_wrap form > div > input:-ms-input-placeholder{color: #a0a0a0; font-size: 1.5rem; font-family: NanumBarunGothic; color: #eee !important;}
.login_wrap form > div > input::placeholder{color: #a0a0a0; font-size: 1.5rem; font-family: NanumBarunGothic; color: #eee !important;}
.login_wrap form > div > input:nth-of-type(1){margin-bottom: 10px;}
.login_wrap form > input{display: block; float: right; width: 79px; height: 90px; font-size: 1.24rem; color: #fff; background: none; line-height: 80px; border: none; cursor: pointer; transition: .2s; margin-bottom: 20px; border: 1px solid #fff;}
.login_wrap form > p{clear: both; margin-bottom: 0;}
.login_wrap form > p input#id_Save{display: none;}
.login_wrap form > p input#id_Save + label{position: relative; display: inline-block; width: 20px; height: 20px; cursor: pointer; background: url(data:image/png;base64,) no-repeat center; background-size: contain;}
.login_wrap form > p input#id_Save:checked + label{background: url(data:image/png;base64,) no-repeat center; background-size: contain;}
.login_wrap form > p input#id_Save + label span{position: absolute; width: 101px; font-size: 1.5rem; color: #fff; top: 1px; left: 26px; font-weight: 300 !important; }
.login_wrap form > span{display: inline-block; width: 100%; height: 1px; background: #b4b4b4; margin: 20px 0;}
.login_wrap form > a{border: 1px solid #fff;; display: block; width: 100%; background: none; height: 40px; border-radius: 7px; cursor: pointer; transition: .2s; font-weight: 300 !important; color: #fff; line-height: 40px; text-align: center; font-size: 1.3rem;}
.login_wrap form > a:hover{text-decoration: none;}
@charset "utf-8";

/*.joinBox*/
.join_wrap{width: 100%; height: calc(100% - 70px); position: relative;}
.join_wrap > form{display: block; width: 950px; position: absolute; top: 40%; left: 50%; -webkit-transform: translate(-50%, -50%); transform: translate(-50%, -50%); }
.join_wrap > form > div{border: 1px solid #fff; padding: 20px 0;}
.join_wrap > form > div:nth-of-type(1){border-bottom: none; padding-bottom: 10px;}
.join_wrap > form > div:nth-of-type(2){border-top: none; border-bottom: none; padding-top: 10px; padding-bottom: 10px;}
.join_wrap > form > div:nth-of-type(3){border-top: none; padding-top: 10px;}
.join_wrap > form div > p{display: inline-block; width: 125px; height: 41px; color: #fff; padding-left: 25px; margin-bottom: 0;}
.join_wrap > form div > p > span:nth-child(1){ font-size: 1.4rem; color: #fff; line-height: 41px;}
.join_wrap > form div > p > span:nth-child(2){color: #e64135; font-size: 1.4rem; display: inline-block; -webkit-transform: translate(2px, 2px); transform: translate(2px, 2px); line-height: 41px;}
.join_wrap > form div > input{width: 250px; height: 33px; padding-left: 10px; font-size: 1.333rem; color: #fff; margin: 4px 0; margin-left: 6px; border: 1px solid #fff; background: transparent;}
.join_wrap > form div > button{width: 140px; height: 33px; border: none; background: none; font-size: 1.4rem; cursor: pointer; margin-left: 6px; margin-top: 4px; color: #fff; border: 1px solid #fff;}
.join_wrap > form > div > div{display: inline-block;}
#root > div > div > div > main > div > div > form > div:nth-child(2) > div:nth-child(1){margin-right: 12px;}
#root > div > div > div > main > div > div > form > div:nth-child(2) > div:nth-child(2) > input[type=text]{width: 217px;}
.join_wrap > form > p{margin-top: 20px; text-align: center; margin-bottom: 0;}
.join_wrap > form > p > a{width: 140px; height: 39px; display: inline-block; color: #fff; font-size: 1.5rem; line-height: 39px; border: 1px solid #fff;}
.join_wrap > form > p > span{width: 140px; height: 39px; display: inline-block; color: #fff; font-size: 1.5rem; cursor: pointer; background: none; line-height: 39px; margin-right: 12px; border: 1px solid #fff; box-shadow: none;}
#root > div > div > div > main > div > div.join_wrap > form > div:nth-child(1) > div:nth-child(1){margin-right: 0px;}
#root > div > div > div > main > div > div.join_wrap > form > div:nth-child(1) > div:nth-child(1) input{color: #fff; cursor: text; background: none;}
@charset "utf-8";

/*.informationBox*/
.information_wrap{width: 100%; height: calc(100% - 70px); position: relative;}
.information_wrap > form{display: block; width: 810px; position: absolute; top: 40%; left: 50%; -webkit-transform: translate(-50%, -50%); transform: translate(-50%, -50%);}
.information_wrap > form > div{border: 1px solid #fff; padding: 20px 0;}
.information_wrap > form > div:nth-of-type(1){border-bottom: none; padding-bottom: 10px;}
